<template>
  <div>
    <footer class="footer">
      <input v-model="message" type="text" placeholder="Написать сообщение..." @keydown.enter="addMess()"/>
        <a href="#!@" v-if="message.length > 0"  @click="addMess()">
          <img src="../assets/img//send.png" alt="" />
        </a>
        <a href="#!@" @click="$emit('clicked', name)" v-else><img src="../assets/img//cam.png" alt="" /></a>
    </footer>
  </div>
</template>

<script>
export default {
  data() {
    return {
      message: "",
    };
  },
  props: {
    name: String
  },
  methods: {
    addMess() {
      let mess = {
        text: this.message,
        time: `${new Date().getHours()}:${new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()}`,
        sender: this.name
      }
      if (this.message != '') {
        this.$emit('addMess', mess)
        this.message = ''
      }
      
    },
  }
};
</script>
