<template>
  <Modal @close="close" :isModalOpen="isOpen" @addUrl="getUrl" name="Misha" />
  <Modal @close="close" :isModalOpen="isModalOpen" @addUrl="getUrl" name="Alex" />
  <div class="chats">
    <div class="alex__chat">
      <Header name="Alex" />
      <Main :messages="allMsg" name="Alex" />
      <Footer @addMess="getMsg" name="Alex" @clicked="click" />
    </div>

    <div class="misha__chat">
      <Header name="Misha" />
      <Main :messages="allMsg" name="Misha" />
      <Footer @addMess="getMsg" name="Misha" @clicked="click" />
    </div>
  </div>
</template>

<script>
import Header from "./components/Header.vue";
import Main from "./components/Main.vue";
import Footer from "./components/Footer.vue";
import Modal from "./components/Modal.vue";

// import First from './components/First.vue'

export default {
  components: {
    Modal,
    Footer,
    Header,
    Main,
  },
  data() {
    return {
      isModalOpen: false,
      isOpen: false,
      message: "",
      allMsg: [],
    };
  },
  methods: {
    getMsg(obj) {
      this.allMsg.push(obj);
    },
    getLocalMsg() {
  let localMsg = localStorage.allMsg;
  if (localMsg) {
    this.allMsg = JSON.parse(localMsg);
  }
},

    getUrl(object) {
      this.allMsg.push(object);
    },
    click(name) {
      if (name == 'Alex') {
        this.isModalOpen = true;
        this.isOpen = false;
      } else {
        this.isModalOpen = false;
        this.isOpen = true;
      }
    },
    close() {
      this.isModalOpen = false;
      this.isOpen = false
    },
  },
  watch: {
    allMsg: {
      handler() {
        localStorage.allMsg = JSON.stringify(this.allMsg)
      },
      deep: true
    }
  },
  mounted() {
    this.getLocalMsg()
  }
};
</script>
