<template>
  <main class="mess">
    <div class="mess__text" :class="{ mess__send: name == message.sender, mess__get: name != message.sender }"
      v-for="(message, key) in messages" :key="key">
      <div>
        <div class="mess__info">
          <img class="mess__img" :src="message.url" alt="" />
          <p>{{ message.text }}</p>
        </div>
        <span class="mess__time">{{ message.time }}</span>
      </div>
    </div>
  </main>
</template>

<script>
export default {
  props: {
    messages: Array,
    name: String,
  },
  data() {
    return {};
  },
};
</script>
