<template>
  <Transition name="fade">
  <div v-show="isModalOpen" @click="closeModal" class="modal">
      <div class="modal__block" @click.stop="">
        <p>Отправить картинку</p>
        <div class="modal__inputs">
          <label>
            <span>URL</span>
            <input @keydown.enter="checkUrl()" v-model="input" type="text"
            placeholder="Введите URL картинки..."/>
          </label>
          <label>
            <span>Комментарий</span>
            <textarea v-model="textarea" @keydown.enter="checkUrl()" @keydown.enter.prevent placeholder="Введите коментарий..."></textarea>
          </label>
        </div>
        <div class="modal__btns">
          <button @click="$emit('close'), this.textarea = '', this.input = ''">Отмена</button>
          <button @click="checkUrl()">Отправить</button>
        </div>
      </div>
    </div>
  </Transition>
</template>
<script>
export default {
  data() {
    return {
      textarea: "",
      input: "",
    };
  },
  props: {
    isModalOpen: Boolean,
    name: String
  },
  methods: {
    closeModal() {
      this.input = "";
      this.textarea = "";
      this.$emit("close");
    },
    addUrl() {
      let mess = {
        url: this.input,
        text: this.textarea,
        time: `${new Date().getHours()}:${new Date().getMinutes() < 10
            ? "0" + new Date().getMinutes()
            : new Date().getMinutes()
          }`,
        sender: this.name,
      };
      if (this.input != "") {
        this.$emit("addUrl", mess);
        this.$emit("close");
        this.input = "";
        this.textarea = "";
      }
    },
    isUrl(str) {
      const urlRegex = /^((ftp|http|https):\/\/|data:)([^ "]+)$/i;
      return urlRegex.test(str);
    },
    checkUrl() {
      if (this.isUrl(this.input)) {
        this.addUrl();
      } else {
        this.input = ""
        alert("Некорректная ссылка!");
      }
    },
  },
};
</script>